
import XHeader from 'vux/src/components/x-header/index.vue'

import Collapse from '@/components/Collapse/Collapse'
import ShowMore from '@/components/ShowMore/showMore'
export default{
data(){
return {
resObj: {}
}
},
mounted(){
this.$vux.loading.show({
text: '加载中...'
})
this.initInfo();
},
methods:{
getString(info){
return typeof info !='undefined' ? info : ''
},
initInfo(){
var routerParams = this.$route.params;

this.$ajax.post('',{
'MSH.1':'weakVisitService',
'MSH.2':'getById',
'SZID':routerParams.SZID,
'YLJGID':routerParams.YLJGID,
'CHZBM':this.$root.loginInfo.CHZBM,
'grdaid':this.$root.loginInfo.grdaid,
'token':this.$root.loginInfo.token,
}).then(data => {
this.$vux.loading.hide()
if(typeof data !='undefined'){
console.log(data)
this.resObj = data;
}
}).catch(error => {
console.log(error);
});

}

},



components: {
XHeader,Collapse,ShowMore
},


}




